import { Button } from "@mui/material";
import React, { useEffect, useState } from "react";
import "./ShowcaseCard.css";
import { Image } from "react-bootstrap";
const ShowcaseCard = (props) => {
  // title size less than 30

  const [title, setTitle] = useState("");
  const [subText, setSubText] = useState("");

  useEffect(() => {
    const truncate = (str, n) => {
      return str?.length > n ? str.substr(0, n - 1) + "..." : str;
    };
    setTitle(truncate(props.title, 30));
    setSubText(truncate(props.subTitle, 50));
  }, [props]);
  return (
    <div className="showcase-card">
      <div className="showcase-card-image">
        <Image src={props.image[0].url} alt="" className="showcase-card-img" />
      </div>

      <div className="showcase-card-info">
        <div className="showcase-black-tint" />
        <h3>{title}</h3>
        <p>{subText}</p>
        <div className="showcase-card-button">
          <Button
            sx={{
              color: "black",
              backgroundColor: "white",
              width: "20%",
              float: "right",
              bottom: "0",
              right: "0",
              position: "absolute",
              zIndex: "2",
              margin: "20px",
              fontSize: "16px",
              ":hover": {
                border: "1px ridge white",
                color: "white",
              },
            }}
            onClick={props.viewClick}
          >
            View
          </Button>
        </div>
      </div>
    </div>
  );
};

export default ShowcaseCard;
