import React from "react";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import NotFound from "./404";
import AboutInfo from "./Components/About/AboutInfo";
import Header from "./Components/NavBar/Header";
import MoreInfo from "./Components/PropertyListing/MoreDetails/MoreInfo";
import PropertyList from "./Components/PropertyListing/Cards/PropertyListing/PropertyList";
import Footer from "./Components/NavBar/Footer";

const Home = () => {
  return (
    <Router basename={"/ideal-venture"}>
      <div className="scrollbar" id="style-3">
        <div>
          <Header />
        </div>
        <div className="overflow">
          <Switch>
            <Route exact path="/property">
              <PropertyList />
            </Route>
            <Route exact path="/moreInfo">
              <MoreInfo />
            </Route>
            <Route exact path="/">
              <AboutInfo />
            </Route>
            <Route path="*">
              <NotFound />
            </Route>
          </Switch>
        </div>
        <div>
          <Footer />
        </div>
      </div>
    </Router>
  );
};

export default Home;
